@import url("https://fonts.googleapis.com/css2?family=Carter+One&display=swap");
.top-banner {
  display: flex;
  justify-content: center;
  background-color: rgba(255, 0, 0, 0.863);
  padding: 15px 0;
  box-shadow: 0px 10px 13px -7px #7c7c7c, 0px 9px 24px 8px rgba(0, 0, 0, 0);

  #title {
    font-size: 60px;
    font-family: "Carter One", cursive;
    color: yellow;
    -webkit-text-stroke: 4px #1b2c62;
    margin: 0 10px;
    cursor: pointer;
  }
}
